header {
    background: var(--primary-background-color);
    display: flex;
    justify-content: space-between;
    /* 平均分配子元素 */
    padding: 1em;
    gap: 1em;
    text-align: left;
    /* 左侧部分左对齐 */

}

/* 为特殊的 "bookstore-logo.png" 图像创建样式 */
.circle-image {
    border-radius: 50%;
    /* 将图像容器变成圆形 */
    width: 100px;
    /* 图像容器的宽度 */
    height: 100px;
    /* 图像容器的高度 */
    object-fit: cover;
    /* 缩放图像以适应容器 */
    /* 添加任何其他你想要的特殊样式 */
}

.header-dropdown-and-cart {
    display: flex;
    align-items: center;
    /* 垂直居中对齐 */
    gap: 1em;
    /* 设置元素之间的间隙 */
}

.text-logo {
    font-family: var(--title-font-family);
    text-align: right;
    text-decoration: none;
    color: black;
}



@media (max-width: 600px) {
    header {
        flex-direction: column;
    }
}



.title-and-search-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    text-align: center;
}

.search-container {
    display: flex;
    align-items: center;
}

.search-bar {
    flex-grow: 1;
    /* 填充剩余空间 */
    margin: 0 auto; /* 水平居中 */
    border-radius: 20px;
    /* 设置搜索栏的四个角为圆形 */
    padding: 5px 10px;
    /* 添加内边距以增加搜索栏的大小 */
    display: flex;
    align-items: center; /* 垂直居中对齐 */

}

.search-button {
    width: auto;
    /* 允许按钮的宽度根据内容自动调整 */
    padding: 5px 10px;
    /* 添加内边距以增加按钮的大小 */
    background-color: #5191fc;
    /* 设置按钮的背景颜色为蓝色 */
    color: white;
    /* 设置按钮文本颜色为白色，以确保可读性 */
    border: none;
    /* 去除按钮边框 */
    border-radius: 20px;
    /* 设置按钮的四个角为圆形 */
    margin-left: auto;
    /* 将元素推到右侧 */
}

.shopping-cart {

    display: inline-block;
    margin-right: 10px;
    /* 可根据需要调整距离 */
}

.cart-count {
    position: absolute;
    top: 10px;
    /* 调整垂直位置，负数表示向上移动 */
    right: 5px;
    /* 调整水平位置，负数表示向左移动 */

    color: white;
    /* 文字颜色 */
    border-radius: 50%;
    /* 圆角 */
    padding: 5px 8px;
    /* 内边距，根据需要调整大小 */
    font-size: 12px;
    /* 文字大小 */
}

.title {
    text-align: right;
}




.search-bar input[type="text"] {
    flex-grow: 1; /* 填充剩余空间，使输入框自动扩展 */
}

.search-bar .image-button {
    margin-left: 10px; /* 将图像按钮与输入框分隔开 */
}

.image-button {
    width: 25px;
    height: auto;
    margin-left: 10px; /* 添加一些间距，使其与搜索栏分隔开 */
}