.category-buttons {
    display: flex;
    flex-direction: row;
    text-align: center;
}

.category-buttons .button {
    display: inline-block;
    padding: 5px 10px;
    background-color: white;
    color: black;
    border: 1px solid black;
    text-decoration: none;
    border-radius: 20px;
    margin-right: 10px;
    /* 可以根据需要调整按钮之间的间距 */
}

.category-buttons .button:hover {
    background-color: black;
    color: white;
}



.category-buttons .selected-category-button {
    background-color: #FCE35E;
    color: black;
}