.header-dropdown {
    position: relative;
}

.categories-button {
    margin-top: 0.25em;
    color: black;
}

.header-dropdown ul {
    background-color: white;
    color: black;
    display: none;
}

.header-dropdown li {
    padding: 0.25em 0.5em;
}

.header-dropdown a {
    color: black;
    text-decoration: none;
}

.header-dropdown li:hover {
    background: lightgray;
}

.header-dropdown:hover ul {
    display: block;
    position: absolute;
    min-width: 8em;
    z-index: 1;
    cursor: pointer;
}