footer {
    background: black;
    display: flex;
    flex-direction: column;
    /* 使用列布局 */
    justify-content: space-between;
    /* 上下对齐 */
    align-items: center;
    /* 左右对齐 */
    gap: 1em;
    padding: 10px;
    /* 添加一些内边距 */
}

.top-row,
.bottom-row {
    display: flex;
    color: white;
}

.links a,
.social-media-icons a {
    margin-right: 50px;
    color: white;
}

/* 调整 Follow Us 文本样式 */
.social-media-icons p {
    margin-right: 10px;
}

.links,
.social-media-icons,
.copyright {
    display: flex;
    align-items: center;
    color: white;
    flex-wrap: wrap;
    /* 允许内容自动换行 */
}

.links a,
.social-media-icons a {
    margin-right: 50px;
    color: white;
    text-decoration: none;
}


/* 调整 Follow Us 文本样式 */
.social-media-icons p {
    margin-right: 10px;
}

.spacer {
    width: 280px;
    /* 调整间隔的宽度 */
}