

.home-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1em;
}

.home-list-li{
    text-align: center;
    cursor: pointer;
}

.home-list-div {
    padding: 0.5em 0;
    background: rgba(0, 0, 0, 0.5); /* last # is percent opacity */
    color: white;
    transform: translateY(-2.25em);
    margin-bottom: -2em;
}
.button {
    color: rgb(0, 0, 0);
    /* 设置按钮文字颜色为白色 */
    padding: 5px 10px;
    /* 添加内边距以增加按钮的大小 */
    border: none;
    /* 去除按钮边框 */
    border-radius: 20px;
    /* 添加圆角 */
    cursor: pointer;
}

