
.book-box {
    display: flex;
    flex-direction: column;
    padding: 1em;
    gap: 0.25em;
    position: relative; /* 设置 book-box 为相对定位 */
    border: 1px solid black; /* 添加1像素的灰色边框 */
    width: 250px; /* 添加固定宽度 */
    box-sizing: border-box;
    align-items: center; /* 水平居中 */
    justify-content: center; /* 垂直居中 */
}

.book-title {
    font-weight: bold;
}

.book-author {
    font-style: italic;
}

.book-image {
    position: relative; /* 设置 book-image 为相对定位 */
}
.button.read-now {
    background-color: rgb(79, 124, 214);
    font-size: 18px;
    font-weight: 500;
    color: white;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
    padding: 8px 30px;
    margin: 0 auto; /* 水平居中按钮 */
    position: absolute; /* 设置按钮为绝对定位 */
    top: 50%; /* 上边距设置为50% */
    left: 50%; /* 左边距设置为50% */
    transform: translate(-50%, 180%); /* 使用 transform 属性将按钮居中 */
    white-space: nowrap; /* 强制文本在同一行内显示 */
}

.button.add-to-cart {
    background-color: #FCE35E;
    font-size: 18px;
    font-weight: 700;
    /* 设置 "Add to Cart" 按钮背景颜色为黄色 */
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.2);
    /* 添加阴影效果 */
    width: 100%; /* 设置按钮宽度为父元素宽度 */
    padding: 8px; /* 调整按钮内边距以适应按钮文本 */
}

.button-container {
    display: flex;
    flex-direction: column; /* 垂直排列按钮 */
    align-items: center; /* 居中对齐按钮 */
}